// Подключение свайпера
import Swiper, { Navigation, Pagination } from 'swiper';
Swiper.use([Navigation, Pagination]);


const servicesSlider = document.querySelector('.services__slider')

if (servicesSlider) {
    let recommendSwiper
    function mobileSlider() {
        if (window.innerWidth <= 768 && servicesSlider.dataset.mobile == 'false') {
            recommendSwiper = new Swiper(servicesSlider, {
                slidesPerView: 1.4,
                slidesPerGroup: 1,
                spaceBetween: 10,
                speed: 500,
                breakpoints: {
                    640: {
                        slidesPerView: 1.8,
                        slidesPerGroup: 1,
                        spaceBetween: 20,
                    },
                },
                slideClass: 'services-slide',
            })

            servicesSlider.dataset.mobile = 'true'
        }

        if (window.innerWidth > 768) {
            servicesSlider.dataset.mobile = 'false'
            if (servicesSlider.classList.contains('swiper-container-initialized')) {
                recommendSwiper.destroy()
            }
        }
    }

    mobileSlider()

    window.addEventListener('resize', () => {
        mobileSlider()
    })
}


new Swiper('.staff-slider', {
    navigation: {
        nextEl: '.staff__arrow-right',
        prevEl: '.staff__arrow-left',
    },
    speed: 500,
    loop: false,
    slidesPerView: 1.4,
    slidesPerGroup: 1,
    spaceBetween: 10,
    simulateTouch: true,
    breakpoints: {
        640: {
            slidesPerView: 1.8,
            spaceBetween: 20,
            loop: false,
        },
        768: {
            slidesPerView: 2.7,
            spaceBetween: 20,
            loop: true,
        },
        961: {
            slidesPerView: 4.6,
            spaceBetween: 20,
            loop: true,
        },
    }
})
